html, body {
  height: 100%;
  margin: 0;
  min-width: 320px; }

body {
  margin: 0;
  padding: 0;
  font-size: 15px;
  min-width: 320px;
  background: #eaf1fa;
  color: #11294d; }

.wrapper {
  min-height: 100%;
  /* Equal to height of footer */
  /* But also accounting for potential margin-bottom of last child */
  margin-bottom: -50px; }
  .wrapper .content .body-content {
    margin-left: 10px;
    margin-right: 10px; }

.footer,
.push {
  height: 50px; }

.footer {
  color: #dde7f7;
  overflow: hidden; }
  .footer .content {
    background-color: #3575d3;
    height: 50px;
    padding-top: 18px;
    padding-left: 10px;
    padding-right: 10px; }

#navbar {
  background: #3575d3;
  overflow: hidden;
  border-bottom: #22529a solid 1px;
  /* Navigation */ }
  #navbar a {
    text-decoration: none;
    color: #d7f0ff; }
  #navbar a:hover {
    color: #fff; }
  #navbar .logo, #navbar .toggle {
    display: inline-block;
    font-size: 2.5ex;
    color: #fff;
    overflow: hidden;
    height: 45px;
    line-height: 45px; }
    #navbar .logo a, #navbar .toggle a {
      color: #fff; }
  #navbar .toggle {
    float: right;
    margin-right: 20px; }
    #navbar .toggle span {
      font-size: 2.5ex;
      cursor: pointer; }
  #navbar .logo-img {
    position: relative;
    width: auto;
    height: 32px;
    vertical-align: middle;
    padding-right: 10px; }
  #navbar .nav {
    display: inline-block;
    margin: 0;
    overflow: hidden;
    text-align: center; }
    #navbar .nav ul {
      margin: 0;
      padding: 0; }
      #navbar .nav ul li {
        display: inline-block;
        list-style-type: none; }
    #navbar .nav li.parent .chaild li {
      display: initial; }
    #navbar .nav li.parent ul.child {
      position: absolute;
      background-color: #5f92dc;
      padding: 5px 10px; }
      #navbar .nav li.parent ul.child li {
        display: block;
        text-align: left; }
        #navbar .nav li.parent ul.child li a {
          text-decoration: none;
          color: #d7f0ff; }
  #navbar .nav > ul > li > a {
    color: #e3e6f5;
    display: block;
    line-height: 2em;
    padding: 0.5em 1em;
    text-decoration: none; }
  #navbar .test {
    display: none; }
  #navbar li.parent > a.linkul::after {
    content: '\25BC';
    font-size: 10px; }

.content {
  margin: 0 auto; }

.child {
  display: none; }

.see {
  display: block; }

.toggle span {
  display: none; }

.toggleul {
  display: block !important; }
  .toggleul ul {
    margin-top: 0;
    margin-bottom: -5px;
    margin-left: 0;
    padding-left: 0;
    padding-bottom: 5px;
    background-color: #0388d4; }
  .toggleul li {
    list-style-type: none;
    margin: 5px 0;
    padding-left: 15px; }

div.body-content {
  padding-bottom: 15px; }
  div.body-content table {
    border-collapse: collapse;
    /*
        tr:first-child {
            th{
                border-bottom: #0268a2 solid 1px;
            }
            
        }
        tr:last-child {
            border-bottom: #0268a2 solid 1px;
        }*/ }
    div.body-content table a {
      color: #0f233d; }
    div.body-content table a:visited {
      color: #225290; }
    div.body-content table th, div.body-content table td {
      padding: 1px 3px;
      border-width: 0; }
      div.body-content table th img, div.body-content table td img {
        height: 22px;
        widows: auto; }
      div.body-content table th a, div.body-content table td a {
        text-decoration: none; }
    div.body-content table hr {
      display: none;
      color: #e6eef9;
      margin: 0; }
    div.body-content table tr:first-child {
      margin-bottom: 0;
      background-color: #c1d6f0; }
      div.body-content table tr:first-child a {
        color: #0f233d; }
    div.body-content table tr:nth-child(even) {
      background-color: #d5e3f5; }
    div.body-content table tr:nth-child(2) {
      height: 2px;
      background-color: #eaf1fa; }

#indexof {
  background-color: #dee9f7; }
  #indexof .homeimg {
    vertical-align: middle; }
  #indexof a {
    text-decoration: none;
    color: #0f233d; }

.addmargin {
  padding: 3px 5px;
  margin-top: 15px;
  margin-bottom: 10px; }

@media only screen and (max-width: 420px) {
  div.body-content table {
    table-layout: fixed;
    width: 99%; }
    div.body-content table td, div.body-content table th {
      overflow: hidden;
      text-overflow: ellipsis;
      vertical-align: middle; }
    div.body-content table tr td:nth-child(3), div.body-content table tr th:nth-child(3), div.body-content table tr td:nth-child(5), div.body-content table tr th:nth-child(5) {
      display: none; }
    div.body-content table tr td:nth-child(1), div.body-content table tr th:nth-child(1) {
      width: 10%; }
    div.body-content table tr td:nth-child(2), div.body-content table tr th:nth-child(2) {
      width: 65%; }
    div.body-content table tr td:nth-child(4), div.body-content table tr th:nth-child(4) {
      width: 25%; } }

@media only screen and (min-width: 421px) and (max-width: 600px) {
  div.body-content table {
    table-layout: fixed;
    width: 99%; }
    div.body-content table td, div.body-content table th {
      overflow: hidden;
      text-overflow: ellipsis;
      vertical-align: middle; }
    div.body-content table tr td:nth-child(5), div.body-content table tr th:nth-child(5) {
      display: none; }
    div.body-content table tr td:nth-child(1), div.body-content table tr th:nth-child(1) {
      width: 5%; }
    div.body-content table tr td:nth-child(2), div.body-content table tr th:nth-child(2) {
      width: 50%; }
    div.body-content table tr td:nth-child(3), div.body-content table tr th:nth-child(3) {
      width: 30%; }
    div.body-content table tr td:nth-child(4), div.body-content table tr th:nth-child(4) {
      width: 15%; } }

@media only screen and (min-width: 601px) and (max-width: 1020px) {
  div.body-content table {
    min-width: 70%; }
  .content {
    width: 100%; }
  #navbar .logo img {
    padding-left: 10px; } }

@media only screen and (min-width: 1021px) {
  .content {
    width: 1020px; }
  div.body-content table {
    min-width: 750px; } }
